/* IMAGE LIGHTBOX SELECTOR */
.imagelightbox {
  cursor: pointer;
  position: fixed;
  z-index: 9999;
  touch-action: none;
  box-shadow: 0 0 3.125em rgba(0, 0, 0, 0.75);
  /* 50 */
}

/* ACTIVITY INDICATION */
.imagelightbox-loading,
.imagelightbox-loading div {
  border-radius: 50%;
}

.imagelightbox-loading {
  width: 2.5em;
  /* 40 */
  height: 2.5em;
  /* 40 */
  background-color: #666;
  position: fixed;
  z-index: 10003;
  top: 50%;
  left: 50%;
  padding: 0.625em;
  /* 10 */
  margin: -1.25em 0 0 -1.25em;
  /* 20 */
  box-shadow: 0 0 2.5em rgba(0, 0, 0, 0.75);
  /* 40 */
}

.imagelightbox-loading div {
  width: 1.25em;
  /* 20 */
  height: 1.25em;
  /* 20 */
  background-color: #fff;
  animation: imagelightbox-loading 0.5s ease infinite;
}

@keyframes imagelightbox-loading {
  from {
    opacity: 0.5;
    transform: scale(0.75);
  }
  50% {
    opacity: 1;
    transform: scale(1);
  }
  to {
    opacity: 0.5;
    transform: scale(0.75);
  }
}

/* OVERLAY */
.imagelightbox-overlay {
  background-color: rgba(255, 255, 255, 0.9);
  position: fixed;
  z-index: 9998;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

/* "CLOSE" BUTTON */
.imagelightbox-close {
  width: 2.5em;
  /* 40 */
  height: 2.5em;
  /* 40 */
  text-align: left;
  background-color: #1b181b;
  border-radius: 50%;
  border: none;
  outline: none;
  position: fixed;
  cursor: pointer;
  z-index: 10002;
  top: 2.5em;
  /* 40 */
  right: 2.5em;
  /* 40 */
  transition: all 0.3s ease-in-out;
}

.imagelightbox-close:hover,
.imagelightbox-close:focus {
  background-color: #666;
}

.imagelightbox-close:before,
.imagelightbox-close:after {
  width: 2px;
  background-color: #fff;
  content: '';
  position: absolute;
  top: 20%;
  bottom: 20%;
  left: 50%;
  margin-left: -1px;
}

.imagelightbox-close:before {
  transform: rotate(45deg);
}

.imagelightbox-close:after {
  transform: rotate(-45deg);
}

/* CAPTION */
