/*--------------------------------------
                  CART
--------------------------------------*/
.cart-loader {
  position: relative;
  top: -2px;
  display: none;
  margin-left: 5px;
  width: 20px;
  height: 20px;
}

.cart-loader img {
  max-width: 100%;
}

.cart-table {
  position: relative;
}

.cart-table:before {
  display: none;
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  z-index: 2;
  position: absolute;
  background-color: rgba(255, 255, 255, 0.8);
}

.cart-table.loading:before {
  display: block;
}

.cart-table .cart-loader {
  position: absolute;
  top: 50%;
  right: 0;
  left: 0;
  margin: auto;
  transform: translateY(-50%);
  display: none;
  z-index: 3;
}

.cart-table .cart-thumbnail {
  width: 40px;
}

.cart-table .form-control, .cart-table #rfs-feal .form-control, #rfs-feal .cart-table .form-control {
  width: 70px;
}

@media all and (max-width: 640px) {
  .cart-table .form-control, .cart-table #rfs-feal .form-control, #rfs-feal .cart-table .form-control {
    display: inline-block;
  }
}

.cart-table .remove-from-cart {
  color: #a70068;
  cursor: pointer;
}

@media all and (max-width: 640px) {
  .responsive-table,
  .responsive-table thead,
  .responsive-table tbody,
  .responsive-table tr,
  .responsive-table td {
    display: block;
  }
  .responsive-table td:before {
    display: block;
    float: left;
    width: 90px;
    content: attr(data-col-name);
    font-weight: 600;
  }
  .responsive-table td.hide-on-mobile {
    display: none;
  }
  .responsive-table td .td-inner {
    overflow: hidden;
  }
  .responsive-table thead {
    position: absolute;
    z-index: -9;
    top: -9999px;
    left: -999px;
  }
}

.cart-summary .cart-summary-content {
  position: relative;
}

.cart-summary .cart-summary-content table td:first-child {
  width: 33%;
}

.cart-summary .cart-summary-content:before {
  display: none;
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  z-index: 2;
  position: absolute;
  background-color: rgba(255, 255, 255, 0.8);
}

.cart-summary .cart-summary-content.loading:before {
  display: block;
}

.cart-summary .cart-summary-content .cart-loader {
  position: absolute;
  top: 50%;
  right: 0;
  left: 0;
  margin: auto;
  transform: translateY(-50%);
  top: calc(50% - 52px);
  display: none;
  z-index: 3;
}

.cart-summary .cart-summary-content .cart-loader img {
  width: 20px;
  height: 20px;
}

.cart-summary table > tbody > tr > td {
  padding: 10px;
  font-weight: 600;
}

.cart-summary table > tbody > tr > td:last-child {
  font-size: 16px;
}

@media all and (max-width: 768px) {
  .cart-summary table > tbody > tr > td:last-child {
    font-size: 14px;
  }
}

.cart-summary table > tbody > tr:last-child > td {
  color: #a70068;
}

