/* IMAGE LIGHTBOX SELECTOR */

.imagelightbox {
  cursor: pointer;
  position: fixed;
  z-index: 9999;
  touch-action: none;
  box-shadow: 0 0 3.125em rgba(0, 0, 0, 0.75); /* 50 */
}

/* ACTIVITY INDICATION */

.imagelightbox-loading,
.imagelightbox-loading div {
  border-radius: 50%;
}

.imagelightbox-loading {
  width: 2.5em; /* 40 */
  height: 2.5em; /* 40 */
  background-color: #666;
  position: fixed;
  z-index: 10003;
  top: 50%;
  left: 50%;
  padding: 0.625em; /* 10 */
  margin: -1.25em 0 0 -1.25em; /* 20 */
  box-shadow: 0 0 2.5em rgba(0, 0, 0, 0.75); /* 40 */
}

.imagelightbox-loading div {
  width: 1.25em; /* 20 */
  height: 1.25em; /* 20 */
  background-color: #fff;
  animation: imagelightbox-loading 0.5s ease infinite;
}

@keyframes imagelightbox-loading {
  from {
    opacity: 0.5;
    transform: scale(0.75);
  }

  50% {
    opacity: 1;
    transform: scale(1);
  }

  to {
    opacity: 0.5;
    transform: scale(0.75);
  }
}

/* OVERLAY */

.imagelightbox-overlay {
  background-color: rgba(255, 255, 255, 0.9);
  position: fixed;
  z-index: 9998;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

/* "CLOSE" BUTTON */

.imagelightbox-close {
  width: 2.5em; /* 40 */
  height: 2.5em; /* 40 */
  text-align: left;
  background-color: #1b181b;
  border-radius: 50%;
  border: none;
  outline: none;
  position: fixed;
  cursor: pointer;
  z-index: 10002;
  top: 2.5em; /* 40 */
  right: 2.5em; /* 40 */
  transition: all 0.3s ease-in-out;
}

.imagelightbox-close:hover,
.imagelightbox-close:focus {
  background-color: #666;
}

.imagelightbox-close:before,
.imagelightbox-close:after {
  width: 2px;
  background-color: #fff;
  content: '';
  position: absolute;
  top: 20%;
  bottom: 20%;
  left: 50%;
  margin-left: -1px;
}

.imagelightbox-close:before {
  transform: rotate(45deg);
}

.imagelightbox-close:after {
  transform: rotate(-45deg);
}

/* CAPTION */

.imagelightbox-caption {
  text-align: center;
  color: #fff;
  background-color: #1b181b;
  position: fixed;
  z-index: 10001;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 0.625em; /* 10 */
}

/* NAVIGATION */

.imagelightbox-nav {
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 20px;
  position: fixed;
  z-index: 10001;
  left: 50%;
  bottom: 3.75em; /* 60 */
  padding: 0.313em; /* 5 */
  transform: translateX(-50%);
}

.imagelightbox-nav button {
  width: 1em; /* 20 */
  height: 1em; /* 20 */
  background-color: transparent;
  border: 1px solid #fff;
  outline: none;
  border-radius: 50%;
  display: inline-block;
  margin: 0 0.313em; /* 5 */
}

.imagelightbox-nav .active {
  background-color: #fff;
}

/* ARROWS */

.imagelightbox-arrow {
  width: 3.75em; /* 60 */
  height: 7.5em; /* 120 */
  background-color: #1b181b;
  vertical-align: middle;
  display: none;
  position: fixed;
  z-index: 10001;
  top: 50%;
  margin-top: -3.75em; /* 60 */
  border: none;
  outline: none;
  cursor: pointer;
  padding-top: 0.2em;
  transition: all 0.3s ease-in-out;
}

.imagelightbox-arrow:hover,
.imagelightbox-arrow:focus {
  background-color: #666;
}

.imagelightbox-arrow:active {
  background-color: #666;
}

.imagelightbox-arrow-left {
  left: 2.5em; /* 40 */
}

.imagelightbox-arrow-right {
  right: 2.5em; /* 40 */
}

.imagelightbox-arrow:before {
  width: 0;
  height: 0;
  border: 1em solid transparent;
  content: '';
  display: inline-block;
  margin-bottom: -0.125em; /* 2 */
}

.imagelightbox-arrow-left:before {
  border-left: none;
  border-right-color: #fff;
  margin-left: -0.313em; /* 5 */
}

.imagelightbox-arrow-right:before {
  border-right: none;
  border-left-color: #fff;
  margin-right: -0.313em; /* 5 */
}

.imagelightbox-loading,
.imagelightbox-overlay,
.imagelightbox-close,
.imagelightbox-caption,
.imagelightbox-nav,
.imagelightbox-arrow {
  animation: fade-in 0.25s linear;
}

@-webkit-keyframes fade-in {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes fade-in {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@media only screen and (max-width: 41.25em) /* 660 */ {
  .imagelightbox-close {
    top: 1.25em; /* 20 */
    right: 1.25em; /* 20 */
  }

  .imagelightbox-nav {
    bottom: 1.25em; /* 20 */
  }

  .imagelightbox-arrow {
    width: 2.5em; /* 40 */
    height: 3.75em; /* 60 */
    margin-top: -2.75em; /* 30 */
  }

  .imagelightbox-arrow-left {
    left: 1.25em; /* 20 */
  }

  .imagelightbox-arrow-right {
    right: 1.25em; /* 20 */
  }
}

@media only screen and (max-width: 20em) /* 320 */ {
  .imagelightbox-arrow-left {
    left: 0;
  }

  .imagelightbox-arrow-right {
    right: 0;
  }
}
